body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    background-color: #2c323c;
    background-image: url("https://d33wubrfki0l68.cloudfront.net/4de356f6ddb204ae047d4fe8bfadd690d4d0b2fe/00972/assets/images/geometry.svg");
    background-repeat: repeat;
    color: #fff;
    min-height: 200px;
    padding: 180px 20px;
    padding-bottom: 140px;
    text-align: center;
}
.header h1{
    margin:0;
    margin-bottom:30px;
    font-size:4rem
}
.navbar .correct{
    -webkit-animation:.4s correct;
    animation:.4s correct
}

.navbar .incorrect{
    -webkit-animation:.4s incorrect;
    animation:.4s incorrect
}

.navbar{
    background:#5133a4;
    min-height:70px;
    position:fixed;
    width:100%;box-shadow:0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22);
    color:#fff;
    font-size:calc(10px + 14 * (100vw - 320px)/ 800);
    z-index:99;
    white-space:nowrap
}

.navbar ul{
    margin:0;
    padding:0
}

.navbar li{
    display:inline-block;
    padding:26px 0;
    width:33.3%;
    text-align:center
}

.navbar a{
    text-decoration:none;
    color:#fff;font-weight:900
}

@media (max-width:420px){
    .navbar li:first-child{
        width:25%
    }
    .navbar li:nth-child(2){
        width:35%
    }
    .navbar li:last-child{
        width:40%
    }
}
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
.container {
    display: flex;
    min-height: 80%;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
}
.card {
    background: #fff;
    border-radius: 2px;
    height: 280px;
    margin: 1rem;
    position: relative;
    width: 280px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}
  
.card > .img-container {
   text-align: center;
   background:#6CADDC;
}

.card > .img-container > img {
   width: 60%
}

.img-container{
   background-size: cover;
   margin:8px;
   border:5px solid #f1f1f1;
   border-radius:3px;
   cursor:pointer
}

@media (min-width:1100px){
  .container{
     padding-left:100px;
     padding-right:100px;
     width:1100px,
  }
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

